import React from "react";
import { inject, observer } from 'mobx-react/index'

import { withRouter, Switch, Redirect, Route, NavLink, Link } from 'react-router-dom'
import { message, Modal, Form, Input, Icon, InputNumber } from "antd";
import { isAuthenticated } from "../../../utils/Session";
import api from "../../../api/api";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'

@inject('articleStore') @observer
class Index extends React.Component {
    constructor(props) {
        super(props)
        // console.log(this.props)
    }
    state = {
        listCategory: [],
        list: [],
        status: 1,
        visible: false,
        visibleEdit: false,
        backUserId: '',
        categoryId: 0,
        isCheck: 1,
        pageNo: 1,
        sortIndex: 0,
        visibleSort: false,
        visibleAddChild: false,
        childColumn: '',
        visibleLookChild: false,
        columnName: '',
        lookChild: '',
        statusChild: 1,
        parentId: 0,
        isShow:false

    }
    componentWillMount() {
        this.setState({
            visible: this.props.location.params ? this.props.location.params.visible : false
        })
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId
        })
        api('listCategory', 'post', {}, { 'type': 1, status: 1 }).then(res => {
            if (!res.data.errorMessage) {
                let arr = res.data.data

                this.setState({
                    listCategory: res.data.data,
                    list: arr,

                })

            } else {
                message.warning(res.data.errorMessage)
                this.setState({
                    isShow:true
                })
            }
        })
    }

    componentDidMount() {
        NProgress.done()

    }
    getList = (val) => {
        let arr = []
        for (let index = 0; index < val.length; index++) {
            const element = val[index];
            arr.push(element)
            arr = arr.concat(this.getList(element.list))
        }
        return arr
    }
    componentWillUnmount() {
        NProgress.start()
    }
    setStatus = (val) => {
        console.log(val)
        NProgress.start()
        this.setState({
            status: val
        })
        api('listCategory', 'post', {}, { 'type': 1, status: val }).then(res => {
            console.log(res)
            NProgress.done()

            if (!res.data.errorMessage) {
                let arr = res.data.data

                this.setState({
                    listCategory: res.data.data,
                    list: arr,
                    pageNo: 1

                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        }).catch(() => {
        })
    }
    setStatusChild = (val) => {
        NProgress.start()
        this.setState({
            statusChild: val
        })
        api('getChildCategory', 'post', {}, { 'type': 1, status: val ,parentId:this.state.parentId}).then(res => {
            console.log(res)
            NProgress.done()

            if (!res.data.errorMessage) {
              
                this.setState({
                    lookChild: res.data.data,
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        }).catch(() => {
        })
    }
    setVisble = (res) => {
        this.props.form.setFieldsValue({
            name: res.name,
        });
        this.setState({
            visible: true,
            isCheck: res.status,
            categoryId: res.id

        })
    }
    handleSaveEdit = () => {
        console.log(1234)
        let name = this.props.form.getFieldsValue(['name'])
        console.log(name)
        if (name.name) {
            if (this.state.visibleLookChild) {
                console.log(this.state.statusChild,this.state.isCheck)
                api('editCategory', 'post', {}, {
                    "categoryName":encodeURI(name.name) ,
                    "adminId": this.state.backUserId,
                    "status": this.state.isCheck == this.state.statusChild ? '' : this.state.isCheck,
                    "categoryId": this.state.categoryId,
                  
                }).then(res => {
                    this.handleCancel()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('编辑成功！')

                        api('getChildCategory', 'post', {}, { 'type': 1, status: this.state.statusChild,parentId:this.state.parentId }).then(res => {
                            if (!res.data.errorMessage) {


                                this.setState({
                                    lookChild:res.data.data,
                                    isCheck:1
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            } else {
                api('editCategory', 'post', {}, {
                    "categoryName": name.name,
                    "adminId": this.state.backUserId,
                    "status": this.state.isCheck == this.state.status ? '' : this.state.isCheck,
                    "categoryId": this.state.categoryId
                }).then(res => {
                    this.handleCancel()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('编辑成功！')

                        api('listCategory', 'post', {}, { 'type': 1, status: this.state.status }).then(res => {
                            if (!res.data.errorMessage) {


                                this.setState({
                                    listCategory: res.data.data,
                                    list: res.data.data,
                                    isCheck:1
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            }
        } else {
            message.warning('请输入栏目名称！')
        }
    }
    setCheck = (val) => {
        this.setState({
            isCheck: val
        })
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    handleOkAddChild = (val) => {
        console.log(val);
        this.setState({
            visibleAddChild: true,
            childColumn: val
        });
    };

    handleCancelLookChild = e => {
        console.log(e);
        this.setState({
            visibleLookChild: false,
        });
    };
    handleOkLookChild = (val) => {
        if (val) {
            api('getChildCategory', 'post', {}, { 'type': 1, status: this.state.status, parentId: val }).then(res => {
                if (!res.data.errorMessage) {


                    this.setState({
                        lookChild: res.data.data,
                        visibleLookChild: true,
                        parentId: val
                    })
                }
            })
        }

    };

    handleCancelAddChild = e => {
        console.log(e);
        this.setState({
            visibleAddChild: false,
        });
    };
    handleOkEdit = e => {
        console.log(e);
        this.setState({
            visibleEdit: false,
        });
    };

    handleCancelEdit = e => {
        console.log(e);
        this.setState({
            visibleEdit: false,
        });
    };
    showModel = (res) => {
        this.setState({
            visibleEdit: true,
            categoryId: res.id
        })
    }
    showModelAdd = () => {
        this.setState({
            visibleEdit: true,
            categoryId: 0
        })
    }

    handleSave = () => {
        let name = this.props.form.getFieldsValue(['nameEdit'])
        console.log(name)
        if (name.nameEdit) {
            api('saveCategory', 'post', {
                "name": name.nameEdit,
                "parentId": this.state.categoryId,
                "status": this.state.isCheck,
                "type": 1
            }).then(res => {
                this.handleCancelEdit()
                if (!res.data.errorMessage) {
                    this.props.form.resetFields()
                    message.success('添加成功！')
                    api('getChildCategory', 'post', {}, { 'type': 1, status: this.state.status }).then(res => {
                        if (!res.data.errorMessage) {


                            this.setState({
                                listCategory: res.data.data,


                            })
                        }
                    })
                } else {
                    message.warning(res.data.errorMessage)
                }
            })
        } else {
            message.warning('请输入栏目名称！')
        }
    }
    handleSaveAddChild = () => {
        let name = this.props.form.getFieldsValue(['nameEdit'])
        console.log(name)
        if (name.nameEdit) {
            if(this.state.visibleLookChild){
                api('saveCategory', 'post', {
                    "name": name.nameEdit,
                    "parentId": this.state.lookChild.id,
                    "status": this.state.isCheck,
                    "type": 1,
                    parentId:this.state.parentId
                }).then(res => {
                    this.handleCancelEdit()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('添加成功！')
                        this.handleCancelAddChild()
    
                        api('getChildCategory', 'post', {}, { 'type': 1, status: this.state.statusChild, parentId: this.state.parentId }).then(res => {
                            if (!res.data.errorMessage) {
    
                                this.setState({
                                    lookChild: res.data.data,
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            }else{
                api('saveCategory', 'post', {
                    "name": name.nameEdit,
                    "parentId": this.state.lookChild.id,
                    "status": this.state.isCheck,
                    "type": 1
                }).then(res => {
                    this.handleCancelEdit()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('添加成功！')
                        this.handleCancelAddChild()
    
                        api('listCategory', 'post', {}, { 'type': 1, status: this.state.status,}).then(res => {
                            if (!res.data.errorMessage) {
    
                                this.setState({
                                    listCategory: res.data.data,
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            }
           
        } else {
            message.warning('请输入栏目名称！')
        }
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {
            this.setState({
                pageNo: this.state.pageNo - 1
            })

        }
    }
    setNext = () => {
        if (this.state.pageNo * 10 >= this.state.list.length) {
            return
        } else {
            this.setState({
                pageNo: this.state.pageNo + 1
            })

        }
    }
    onChange = (value) => {
        console.log('changed', value);
    }
    setSort = (val) => {
        //   this.state.list[index].isSort=2
        console.log(val)
        this.props.form.setFieldsValue({ 'nameSort': val.erialNum })
        this.setState({
            visibleSort: true,
            sortIndex: val.erialNum,
            categoryId: val.id
        })
    }
    handleOkSort = () => {
        this.setState({
            visibleSort: true
        })
    }
    onCancelSort = () => {
        this.setState({
            visibleSort: false
        })
        //   erialCategory
    }
    handleSaveSort = () => {
        let name = this.props.form.getFieldsValue(['nameSort'])
        console.log(name)
        if (name.nameSort) {
            if (this.state.visibleLookChild) {
                api('erialCategory', 'post', {}, {
                    "erialNum": name.nameSort,
                    "adminId": this.state.backUserId,
                    'parentId': this.state.parentId,
                    "categoryId": this.state.categoryId
                }).then(res => {
                    this.handleCancel()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('编辑成功！')
                        this.onCancelSort()
                        api('getChildCategory', 'post', {}, { 'type': 1, status: this.state.statusChild, 'parentId': this.state.parentId, }).then(res => {
                            if (!res.data.errorMessage) {


                                this.setState({

                                    lookChild: res.data.data,
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            } else {
                api('erialCategory', 'post', {}, {
                    "erialNum": name.nameSort,
                    "adminId": this.state.backUserId,
                    "categoryId": this.state.categoryId
                }).then(res => {
                    this.handleCancel()
                    if (!res.data.errorMessage) {
                        this.props.form.resetFields()
                        message.success('编辑成功！')
                        this.onCancelSort()
                        api('listCategory', 'post', {}, { 'type': 1, status: this.state.status, }).then(res => {
                            if (!res.data.errorMessage) {


                                this.setState({
                                    listCategory: res.data.data,
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
            }
        } else {
            message.warning('请输入序号！')
        }
    }
    onChangeSort = (value) => {
        console.log(value)
    }
    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <div id="column">
                <div  className={this.state.isShow?'hide':'statusBox'}>
                    <div className="articleEditBox">
                        <span className={this.state.status == 1 ? 'status-active' : ''} onClick={() => this.setStatus(1)}>上架中</span>
                        <span className={this.state.status == 2 ? 'status-active' : ''} onClick={() => this.setStatus(2)}>下架中</span>
                        <span className='add' onClick={this.showModelAdd}>+ 添加类目</span>
                    </div>
                </div>
                <div  className={this.state.isShow?'hide':'list'}>
                    <div className="list_head flexBox">
                        <div className="num">
                            序号
                        </div>
                        <div className="name">
                            名称
                        </div>
                        <div className="status">
                            状态
                        </div>
                        <div className="operation flex">
                            操作
                        </div>
                    </div>
                    {
                        this.state.list.map((res, index) => {
                            if (index >0&&index >= (this.state.pageNo-1 ) * 10 && index <= (this.state.pageNo) * 10) {
                                return (
                                    <div className="list_info flexBox"key={index}>
                                        <div className="num">
                                            {index <= 9 ? index  == 10 ? index + 1 : '0' + (index ) : index>=10?index+1:index }
                                        </div>
                                        <div className="name">
                                            {res.name}
                                        </div>
                                        <div className="status">
                                            {res.status == 1 ? '上架中' : '下架中'}
                                        </div>
                                        <div className="operation flex">
                                            <div onClick={() => {
                                                this.setVisble(res)
                                            }}>编辑</div>
                                            <div className='sort'>
                                                <span onClick={() => this.setSort(res)}>排序</span>

                                            </div>
                                            <span onClick={() => {
                                                this.handleOkLookChild(res.id)
                                            }}>查看子类目</span>
                                        </div>
                                    </div>
                                )
                            }
                        })
                    }
                    {
                            this.state.list.length==0?<div className='noData'>
                                    <img src={require('../../../assets/images/img-nodata.png')} alt=""/>
                                    <p>暂无相关信息</p>
                                </div>:''
                        }
                    {
                        this.state.list.length <= 10 ? '' : <div className='pagination'>
                            <span onClick={this.setPrev}>
                                <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                            <span>
                                {this.state.pageNo}/{Math.ceil(this.state.list.length / 10)}
                            </span>
                            <span onClick={this.setNext}>
                                <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                        </div>
                    }
                </div>
                <Modal
                    title=""
                    visible={this.state.visible}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width='52%'
                    wrapClassName='editColumn'
                >
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="类目名称:">
                            {getFieldDecorator('name', {

                            })(<Input autocomplete="off"/>)}
                        </Form.Item>
                        <div className="flexBox">
                            <div className="ant-col-5" style={{ 'textAlign': 'right', 'paddingRight': '8px' }}>是否启用<span>:</span></div>
                            <div className="ant-col-12 flexBox">
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(1) }}>
                                        <span className={this.state.isCheck == 1 ? 'active' : ''}>
                                            {this.state.isCheck == 1 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        是
                                    </span>
                                </div>
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(2) }}>
                                        <span className={this.state.isCheck == 2 ? 'active' : ''} >
                                            {this.state.isCheck == 2 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        否
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div className="btn">
                            <span className='active' onClick={this.handleSaveEdit}>确认</span>
                            <span onClick={this.handleCancel}>取消</span>
                        </div>
                    </Form>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.visibleEdit}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOkEdit}
                    onCancel={this.handleCancelEdit}
                    width='52%'
                    wrapClassName='editColumn'
                >
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="类目名称:">
                            {getFieldDecorator('nameEdit', {

                            })(<Input autocomplete="off"/>)}
                        </Form.Item>
                        <div className="flexBox">
                            <div className="ant-col-5" style={{ 'textAlign': 'right', 'paddingRight': '8px' }}>是否启用<span>:</span></div>
                            <div className="ant-col-12 flexBox">
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(1) }}>
                                        <span className={this.state.isCheck == 1 ? 'active' : ''}>
                                            {this.state.isCheck == 1 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        是
                                    </span>
                                </div>
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(2) }}>
                                        <span className={this.state.isCheck == 2 ? 'active' : ''} >
                                            {this.state.isCheck == 2 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        否
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div className="btn">
                            <span className='active' onClick={this.handleSave}>确认</span>
                            <span onClick={this.handleCancelEdit}>取消</span>
                        </div>
                    </Form>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.visibleSort}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOkSort}
                    onCancel={this.onCancelSort}
                    width='52%'
                    wrapClassName='editColumn'
                >
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="排序:">
                            {getFieldDecorator('nameSort', {

                            })(<InputNumber min={1} defaultValue={this.state.sortIndex} onChange={this.onChangeSort} />)}
                        </Form.Item>

                        <div className="btn">
                            <span className='active' onClick={this.handleSaveSort}>确认</span>
                            <span onClick={this.onCancelSort}>取消</span>
                        </div>
                    </Form>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.visibleAddChild}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOkAddChild}
                    onCancel={this.handleCancelAddChild}
                    width='52%'
                    wrapClassName='editChildColumn'
                >
                    <div className="title"></div>
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
                        <Form.Item label="类目名称:">
                            {getFieldDecorator('nameEdit', {

                            })(<Input autocomplete="off"/>)}
                        </Form.Item>
                        <div className="flexBox">
                            <div className="ant-col-5" style={{ 'textAlign': 'right', 'paddingRight': '8px' }}>是否启用<span>:</span></div>
                            <div className="ant-col-12 flexBox">
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(1) }}>
                                        <span className={this.state.isCheck == 1 ? 'active' : ''}>
                                            {this.state.isCheck == 1 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        是
                                    </span>
                                </div>
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(2) }}>
                                        <span className={this.state.isCheck == 2 ? 'active' : ''} >
                                            {this.state.isCheck == 2 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        否
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div className="btn">
                            <span className='active' onClick={this.handleSaveAddChild}>确认</span>
                            <span onClick={this.handleCancelAddChild}>取消</span>
                        </div>
                    </Form>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.visibleLookChild}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOkLookChild}
                    onCancel={this.handleCancelLookChild}
                    width='52%'
                    wrapClassName='editLookChild'
                >
                    <div className="articleEditBox">
                        <span className={this.state.statusChild == 1 ? 'status-active' : ''} onClick={() => this.setStatusChild(1)}>上架中</span>
                        <span className={this.state.statusChild == 2 ? 'status-active' : ''} onClick={() => this.setStatusChild(2)}>下架中</span>
                        <span className='add' onClick={() => { this.handleOkAddChild(this.state.lookChild.id) }}>+ 添加子类目</span>
                    </div>
                    <div className="list">
                        <div className="list_head flexBox">
                            <div className="num">
                                序号
                        </div>
                            <div className="name">
                                名称
                        </div>
                            <div className="status">
                                状态
                        </div>
                            <div className="operation flex">
                                操作
                        </div>
                        </div>
                        {
                            this.state.lookChild && this.state.lookChild.map((res, index) => {
                                if (index >= (this.state.pageNo - 1) * 10 && index < (this.state.pageNo) * 10) {
                                    return (
                                        <div className="list_info flexBox">
                                            <div className="num">
                                                {index <= 9 ? index + 1 == 10 ? index + 1 : '0' + (index + 1) : index + 1}
                                            </div>
                                            <div className="name">
                                                {res.name}
                                            </div>
                                            <div className="status">
                                                {res.status == 1 ? '上架中' : '下架中'}
                                            </div>
                                            <div className="operation flex">
                                                <div onClick={() => {
                                                    this.setVisble(res)
                                                }}>编辑</div>
                                                <div className='sort'>
                                                    <span onClick={() => this.setSort(res)}>排序</span>

                                                </div>

                                            </div>
                                        </div>
                                    )
                                }
                            })
                        }
                        {
                            this.state.lookChild.length==0?<div className='noData'>
                                    <img src={require('../../../assets/images/img-nodata.png')} alt=""/>
                                    <p>暂无相关子类目</p>
                                </div>:''
                        }
                    </div>


                </Modal>

            </div>
        )
    }
}
const WrappedApp = Form.create({ name: 'coordinated' })(Index);
export default WrappedApp